.sidebarContainer {
    font-size: 50px;
    width: 15vw;
    position: sticky;
    top:0;
    display: flex;
    flex-wrap: wrap;
    padding: 2*$spacing $spacing 2*$spacing 2*$spacing;
}

.logo {
    width: 50%;
}

.logoContainer {
    width: 100%;
    display: flex;
    justify-content: center;
}

.title {
    text-align: center;
    font-size: x-large;
    width: 100%;
    font-variant: small-caps;
    font-weight: 600;
}
